.singlemapdand-model{
    min-height: 100vh;
    width: 100%;
    height: 100%;
    padding: 6vh 4.1vw;
}

.singlemapdand-model > h1{
    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-size: 5vh;
    color: #03AA6B;
}

.single-m-main{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    margin-top: 4vh;
    gap: 1vw;
}

.single-m-left{
    width: 60%;
    height: 100%;
    
    
}

.single-m-right{
    width:30%;
    height: 100%;
}

.single-m-details{
    letter-spacing: 1px;
    line-height: 1.8rem;
   
}

@media (max-width: 600px) {
    .singlemapdand-model{
        min-height: 100vh;
        width: 100%;
        height: 100%;
        padding: 16vh 4.1vw;
    }
    
    .singlemapdand-model > h1{
        text-align: center;
        font-family: "Montserrat", sans-serif;
        font-size: 1.6rem;
        color: #03AA6B;
    }
    
    .single-m-main{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        margin-top: 4vh;
        gap: 1vw;
    }
    
    .single-m-left{
        width: 100%;
        height: 100%;
        
        
    }
    
    .single-m-right{
        width:100%;
        height: 100%;
    }
    
    .single-m-details{
        letter-spacing: 1px;
        line-height: 1.8rem;
       
    }
}