.mapdandUpdate-items{
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: .5rem
}


.dashboard-items h2{
    text-align: center;
}

.mapdandUd-right{
    width: 69%;
    min-height: 100vh;
    height: 100%;
    padding: 1rem .5rem;

}

.mandand-description{
    height: 30rem;
}

.mapdandUd-left{
    width: 30%;
    min-height: 100vh;
    height: 100%;
    border: 1px solid black;    
    
}

.polyline-update{
    display: flex;
    flex-direction: column;
    padding: 1rem .5rem;
    gap: 3vh;
}

.mapdandUd-left h3{
    text-align: center;
    letter-spacing: 2px;
}

.mapdandUd-polyline-details{
    display: flex;
    gap: .7rem;
}

.mapdandUd-btn{
    width: 49%;
    
}

.polyline-update:first-child{
    margin-bottom: 7%;
}

.marker-div{
    display: flex;
    gap: 2rem;
    align-items: center;
    justify-content: space-between;
}

.mapdand-delete{
    height: 1.5rem;
    width: 1.5rem;
    cursor: pointer;
}

.marker-div h3{
    text-transform: capitalize;
    letter-spacing: 1px;
    cursor: pointer;
}


@media (max-width: 600px) {
    .mapdandUpdate-items{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        gap: .5rem
    }
    
    
    .dashboard-items h2{
        text-align: center;
    }
    
    .mapdandUd-right{
        width: 100%;
        min-height: 100vh;
        height: 100%;
        padding: 1rem .5rem;
    
    }
    
    .mandand-description{
        height: 30rem;
    }
    
    .mapdandUd-left{
        width: 100%;
        min-height: 100vh;
        height: 100%;
        border: 1px solid black;    
        
    }
    
    .polyline-update{
        display: flex;
        flex-direction: column;
        padding: 1rem .5rem;
        gap: 3vh;
    }
    
    .mapdandUd-left h3{
        text-align: center;
        letter-spacing: 2px;
    }
    
    .mapdandUd-polyline-details{
        display: flex;
        gap: .7rem;
    }
    
    .mapdandUd-btn{
        width: 49%;
        
    }
    
    .polyline-update:first-child{
        margin-bottom: 7%;
    }
    
    .marker-div{
        display: flex;
        gap: 2rem;
        align-items: center;
        justify-content: space-between;
    }
    
    .mapdand-delete{
        height: 1.5rem;
        width: 1.5rem;
        cursor: pointer;
    }
    
    .marker-div h3{
        text-transform: capitalize;
        letter-spacing: 1px;
        cursor: pointer;
    }
}