.geocoder-container {
    position: absolute;
    top: 5vh;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1; /* Ensure it appears above the map */
    width: 300px; /* Adjust width as needed */
  }

  .map-div-model{
    min-height: 100vh;
    height: 100%;
    position: relative;
    z-index: 10;
  }

  .main-map{
    height: 100vh;
  
  }

  .calculation-box{
   height: 8rem !important;
    z-index: 20;
  }

  @media (max-width: 600px) {

    .geocoder-container {
      width: 200px;
      left: 40%;
    }
  }